<template>
  <el-container class="ard">
    <Header title="文章详情"/>
    <el-main class="ard-main cm-vertical cm-flex">
      <h3>{{article.title}}</h3>
      <aside class="cm-horizon ard-info cm-padding-v">
        <el-link type="primary">{{articleType}}</el-link>
        <span class="cm-padding-h">{{dateFormat(article.updatedAt)}}</span>
        <el-link type="primary" @click="$linkTo(`/ArticleAddOrEdit/${article.type}/${article.objectId}`)" v-if="admin">
          编辑
        </el-link>
      </aside>
      <article class="ard-content cm-vertical cm-full-w cm-overflow-y">
        <div v-html="article.content"></div>
      </article>
      <aside class="cm-horizon cm-full-w ard-count-info cm-border-b cm-padding-v">
        <el-link>评论:10</el-link>
        <el-link class="cm-padding-h">点赞:30</el-link>
      </aside>
      <article class="cm-padding-v cm-full-w cm-font-xs">
        <section class="cm-horizon ard-comment-cont" v-for="index in 3">
          <aside class="cm-vertical cm-padding-h">
            <el-avatar src=""/>
            <span class=" cm-color-green">zhcin</span>
          </aside>
          <main class="cm-flex cm-align-l cm-border-b">
            <div>comment,这将会是一大串文字，comment,这将会是一大串文字comment,这将会是一大串文字comment,这将会是一大串文字</div>
            <div class="cm-align-c cm-full-w cm-bg-grey ard-more">
              <el-link type="primary" href="">共10条回复</el-link>
            </div>
            <footer class="cm-horizon ard-comment-info">
              <span class="cm-flex">2021-07-27</span>
              <div>
                <i class="el-icon-chat-dot-square"></i>
                <i class="el-icon-star-off cm-padding-h"></i>
              </div>
            </footer>
          </main>
        </section>
      </article>
    </el-main>
    <el-footer>
      <el-button-group class="cm-full-w cm-horizon">
        <el-button class="cm-flex" type="primary" @click="showComment"><i class="el-icon-chat-dot-square"></i> 评论
        </el-button>
        <el-button class="cm-flex" type="primary"><i class="el-icon-star-off"></i> 赞</el-button>
      </el-button-group>
    </el-footer>
    <aside class="ard-mask cm-vertical cm-full cm-center" v-if="showCommentDialog">
      <main class="ard-mask-main cm-bg-white">
        <header class="cm-align-l cm-padding">评论</header>
        <el-input type="textarea" v-model="comment" resize="none" class="cm-radius-n cm-padding" :rows="4"></el-input>
        <el-footer>
          <el-button-group class="cm-full-w cm-horizon">
            <el-button class="cm-flex cm-radius-n" @click="cancelComment">取消</el-button>
            <el-button class="cm-flex cm-radius-n" @click="submitComment">提交</el-button>
          </el-button-group>
        </el-footer>
      </main>
    </aside>
  </el-container>
</template>

<script>
  import {AV} from '../../common/api';
  import {formatDate} from '../../common/utils';
  import Model from '../../model/';
  export default {
    components: {}, computed: {
      articleType(){
        return Model['articleType'][this.article.type];
      }
    },
    methods: {
      getArticleDetail(){
        const {objectId} = this.$route.params;
        const loading = this.$loading(this.$loadingConfig)
        AV.getData('articleList', objectId).then(res => {
          this.article = res;
        }).catch(e => {
          this.$alert('提交失败,' + e);
        }).finally(() => {
          loading.close()
        })
      },
      isAdmin(){
        AV.isAdmin().then((is) => {
          this.admin = is;
        })
      },
      dateFormat(date){
        return formatDate(date, false);
      },
      showComment(){
        this.showCommentDialog = true;
      },
      cancelComment(){
        this.comment = '';
        this.showCommentDialog = false;
      },
      submitComment(){

      }
    },
    data(){
      return {
        article: {},
        admin: false,
        comment: '',
        showCommentDialog: false
      }
    },
    created(){

    },
    mounted(){
      this.getArticleDetail();
      this.isAdmin();
    },
  }
</script>

<style>
  .ard {
  }

  .ard-info {
    width: 100%;
    font-size: var(--font-sm);
    color: grey;
    align-items: center;
  }

  .ard-content {
    min-height: 200px;
    padding: 10px;
    text-align: left;
    background-color: #ebeef5;
  }

  .ard-content p {
    text-indent: 20px;
  }

  .ard-main {
    align-items: flex-start;
    width: 100%;
  }

  .ard-count-info {
    justify-content: flex-end;
  }

  .ard-comment-info {
    color: #929292;
    padding-bottom: 5px;
  }

  .ard-comment-cont {
    margin-bottom: 10px;
  }

  .ard-more {
    padding: 5px 0;
    margin: 5px 0;
  }

  .ard-mask {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 9999;
    background-color: rgba(0, 0, 0, 0.7);
  }

  .ard-mask-main {
    width: 70%;
    min-width: 200px;
    max-width: 300px;
  }
</style>



